import Vue from "vue";
import Vuex from 'vuex';
import axios from 'axios';
Vue.use(Vuex);
export default new Vuex.Store({
    state: {
        login: false,
        id: "",
        email: "",
        name: "",
        favor: [],
        image: []
    },
    getters: {
        isLogin(state) { return state.login; },
        getUID(state, getters) { return getters.isLogin ? state.id : null; },
        getUsername(state, getters) { return getters.isLogin ? state.name : null; },
        getFavorList(state) {
            return state.favor ? state.favor : [];
        }
    },
    mutations: {
        setUserinfo(state, userinfo) {
            for (const prop in state) {
                if (userinfo.hasOwnProperty(prop)) {
                    state[prop] = userinfo[prop];
                }
            }
            if (userinfo["token"]) {
                sessionStorage.setItem("token", userinfo["token"]);
                sessionStorage.setItem("id", userinfo["id"]);
            }
            state.login = true;
        },
        clearUserinfo(state) {
            for (const prop in state) {
                state[prop] = null;
            }
            sessionStorage.clear();
            state.login = false;
        }
    },

    actions: {
        login(context, form) {
            return new Promise((resolve, reject) => {
                axios
                    .post("./api/Login.php", form)
                    .then(function (response) {
                        let $receive = response["data"];
                        if ($receive["actionState"]) {
                            context.commit("setUserinfo", $receive);
                            resolve();
                        } else {
                            reject($receive["error"]);
                        }
                    })
                    .catch(function (error) { reject(error); });
            })
        },
        logout(context) {
            context.commit("clearUserinfo");
        },
        signup(state, form) {
            axios
                .post("", { email: form.email, name: form.name, password: form.password })
                .then(response => {
                    let $receive = response.data;
                    if ($receive.actionState) {
                        this.commit('login', { email: form.email, password: form.password });
                    } else {
                        console.log($receive.error);
                    }
                })
                .catch(error => {
                    console.log(error);
                });
        }
    },
});